.Converter {
    height: 250px;
}
.block {
    flex: 1 0 50%;
    padding: 20px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}
.title {
    font-size: 18px;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
    text-shadow: 1px 1px 1px rgb(113, 113, 113);
}
.inputs {
    text-align: center;
    display: flex;

    justify-content: space-between;
}
.value {
    width: 100%;
}
.value,
.currency {
    // margin-top: 7px;
    font-size: 24px;
    padding: 7px;
    border: 1px solid rgb(46, 46, 46);
    border-radius: 3px;
}
.inputContainer {
    width: 70%;
    margin-right: 20px;
    position: relative;
}
.reset:hover {
    scale: 1.2;
}
.reset {
    color: rgb(0, 0, 0);
    font-weight: 600;
    font-size: 18px;
    position: absolute;
    top: calc(50% - 9px);
    right: 8px;
    cursor: pointer;
}
@media (max-width: 565px) {
    .Converter {
        width: 100%;
    }
    .inputs {
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        input,
        select {
            width: 100%;
        }
    }
    .inputContainer {
        width: 100%;
        margin: 0;
    }
  
}
